.hero-gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at center, rgba(223, 255, 223, 0.6) 0%, rgba(255, 255, 255, 0) 70%);
  z-index: 0;
  pointer-events: none;
}
.hero-text-gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 60%;
  background: radial-gradient(ellipse at left center, rgba(223, 255, 223, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
  z-index: 0;
  pointer-events: none;
}
.hero-section-gradient {
  position: absolute;
  top: 7%;
  right: -8%;
  width: 70%;
  height: 103%;
  background: radial-gradient(ellipse 85% 100% at center, rgba(223, 255, 223, 0.5) 0%, rgba(183, 235, 183, 0.4) 30%, rgba(144, 215, 144, 0.3) 50%, rgba(105, 195, 105, 0.2) 65%, rgba(255, 255, 255, 0) 80%);
  border-radius: 50%;
  filter: blur(60px);
  z-index: 1;
  pointer-events: none;
}
.hero-image-glow {
  position: relative;
}
.hero-image-glow::before {
  content: '';
  position: absolute;
  top: -20px;
  left: -20px;
  right: -20px;
  bottom: -20px;
  background: radial-gradient(ellipse at center, rgba(144, 215, 144, 0.3) 0%, rgba(183, 235, 183, 0.15) 40%, rgba(223, 255, 223, 0.05) 60%, rgba(255, 255, 255, 0) 80%);
  border-radius: 32px;
  filter: blur(30px);
  z-index: -1;
  pointer-events: none;
}
.hero-image-glow::after {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  background: linear-gradient(45deg, rgba(144, 215, 144, 0.08) 0%, rgba(183, 235, 183, 0.04) 50%, rgba(223, 255, 223, 0.08) 100%);
  border-radius: 20px;
  z-index: -1;
  pointer-events: none;
}
